/* ✅ Mise en page générale */
.superadmin-dashboard {
    width: 90%;
    max-width: 1400px;
    margin: auto;
    padding: 20px;
    text-align: center;
}

/* ✅ Titre principal */
.dashboard-title {
    font-size: 28px;
    font-weight: bold;
    color: #333;
}

/* ✅ Message d'accueil */
.dashboard-message {
    font-size: 18px;
    color: #666;
    margin-bottom: 20px;
}

/* ✅ Boutons principaux */
.admin-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
}

.admin-buttons button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.btn-dashboard-user {
    background-color: #007bff;
    color: white;
}

.btn-add-user {
    background-color: #ffc107;
    color: black;
}

/* ✅ Séparation des sections */
.section-title {
    font-size: 22px;
    margin-top: 30px;
    text-align: left;
    padding: 10px;
    background-color: #f8f8f8;
    border-left: 5px solid #007bff;
}

/* ✅ Conteneur des cartes */
.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

/* ✅ Cartes des utilisateurs */
.user-card {
    width: 300px; /* Ajustable */
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    text-align: left;
}

/* ✅ Style des boutons des cartes */
.card-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}

.card-buttons button {
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
}

.btn-edit {
    background-color: #ffc107;
    color: black;
}

.btn-delete {
    background-color: #dc3545;
    color: white;
}

.btn-view {
    background-color: #17a2b8;
    color: white;
}